const mew = {
	imgBig: `
		<img src='data:image/webp;base64,'/>
	`,
	label: 'MEW',
}

export function getWalletInfo(providerType: SupportedWalletProviders): WalletInfo {
	switch (providerType) {
		case SupportedWalletProviders.MetaMask:
			return getEvmInjectedProviderInfo()
		case SupportedWalletProviders.WalletConnect:
			return {
				name: providerType,
				label: 'WalletConnect',
				imgBig: walletConnectSVG,
			}
		case SupportedWalletProviders.WalletConnectV2:
			return {
				name: providerType,
				label: 'WalletConnect V2',
				imgBig: walletConnectV2SVG,
			}
		case SupportedWalletProviders.Torus:
			return {
				name: providerType,
				label: 'Torus',
				imgBig: torusSVG,
			}
		case SupportedWalletProviders.Phantom:
			return getSolanaInjectedProviderInfo()
		case SupportedWalletProviders.Flow:
			return {
				name: providerType,
				label: 'Flow',
				imgBig: flowSVG,
			}
		case SupportedWalletProviders.SafeConnect:
			return {
				name: providerType,
				label: 'SafeConnect',
				imgBig: safeConnectSVG,
			}
	}
}

function getEvmInjectedProviderInfo() {
	const browser = getBrowserData()

	// default fallback icon
	let injectedWallet = ethereumWallet

	if (browser.brave) injectedWallet = braveWallet
	if (browser.anyMetamask) injectedWallet = metaMask
	if (browser.mew) injectedWallet = mew
	if (browser.trust) injectedWallet = trustWallet
	if (browser.status) injectedWallet = statusWallet
	if (browser.alphaWallet) injectedWallet = alphaWallet
	if (browser.imToken) injectedWallet = imToken

	return {
		name: SupportedWalletProviders.MetaMask,
		...injectedWallet,
	}
}

function getSolanaInjectedProviderInfo() {
	return window.solana.isBraveWallet
		? {
				name: SupportedWalletProviders.Phantom,
				label: 'Brave (Solana)',
				imgBig: braveWallet.imgBig,
		  }
		: {
				name: SupportedWalletProviders.Phantom,
				label: 'Phantom',
				imgBig: phantomSVG,
		  }
}
